.personalization-button {
  max-width: 300px;
  padding-bottom: 30px;

  & .button {
    min-height: 40px;
    line-height: 40px;
    font-size: $f-size-xl;
    padding: 0 30px;
    min-width: 160px;
    width: 100%;
    white-space: normal;
    background: $c-gray url(data:image/svg+xml;utf8;base64,) no-repeat 23% center;
  }
}